import logo from './logo.svg';
import './App.css';
import NavBar from './Components/NavBar/NavBar';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


function App() {
  return (
    <Router>
      <NavBar/>
      <Switch>
        <Route path="/noTocar">
          Dije no tocar
        </Route>
        <Route path="/proximamente">
          Próximamente bolo
        </Route>
        <Route path="/tampocotocar">
          Tampoco tocar DIJE
        </Route>
      </Switch>
    </Router>

  );
}

export default App;
